@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1:wght@300;600&display=swap);
.Comment{font-weight:bold}.Comment__list{list-style:none;padding:30px;display:grid;grid-gap:30px;gap:30px}.Comment__item{display:grid;padding:15px;grid-gap:5px;gap:5px;border:2px solid black}.Comment__title{font-size:1rem}.Comment__text{font-size:0.75rem;font-weight:300}.Comment__mail{font-size:0.5em;font-weight:bold}

.User{display:flex;align-items:center;grid-gap:10px;gap:10px}.User__profile-photo{border-radius:50%;height:32px;width:32px;background-color:black}

.Post{padding:50px}.Post__list{list-style:none;padding:0;margin:0;display:grid;grid-gap:50px;gap:50px}.Post__item{padding:20px;background-color:aquamarine;display:grid;grid-gap:20px;gap:20px}.Post__text{font-weight:300}

body,h1,h2,h3,h6{margin:0}.App{font-family:'M PLUS 1', sans-serif;background-color:black}

